import React from 'react';
import { graphql } from 'gatsby';

import {
  Box,
  Text,
  Link,
  Header,
  InlineList,
  InlineListItem,
} from '../components/core';

const Nav = ({ showTitle, hideHome }) => {
  // we use external links so the # works properly on the home page.
  // but on other pages, use normal js routing links for better ux.
  // lazily check if home page by saying we aren't showing the title... sry
  const isExternalLinks = !showTitle;
  return (
    <Box p={3} mx="auto" maxWidth={800}>
      <Text textAlign={'center'}>
        {showTitle && <Header>Peter Beshai</Header>}
        <InlineList>
          {!hideHome && (
            <InlineListItem>
              <Link to="/" activeClassName="active">
                Home
              </Link>
            </InlineListItem>
          )}
          <InlineListItem>
            <Link
              external={isExternalLinks}
              to="/#writing"
              activeClassName="active"
            >
              Writing
            </Link>
          </InlineListItem>
          <InlineListItem>
            <Link
              external={isExternalLinks}
              to="/#projects"
              activeClassName="active"
            >
              Projects
            </Link>
          </InlineListItem>
          <InlineListItem>
            <Link
              external={isExternalLinks}
              to="/#experiments"
              activeClassName="active"
            >
              Experiments
            </Link>
          </InlineListItem>
          <InlineListItem>
            <Link
              external={isExternalLinks}
              to="/#open-source"
              activeClassName="active"
            >
              Open Source
            </Link>
          </InlineListItem>
        </InlineList>
      </Text>
    </Box>
  );
};

export default Nav;
